import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
//import App from './App';

//Body
const myfirstelement = <h3>Hello React!</h3>
ReactDOM.render(myfirstelement, document.getElementById('root'));

//Conatct
class MyForm extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			username: '',
			age: null,
			errormessage: ''
		};
	}
	mySubmitHandler = (event) => {
		event.preventDefault();
		alert("This will submit: " + this.state.username + " " + this.state.age);
	}
	myChangeHandler = (event) => {
		let nam = event.target.name;
		let val = event.target.value;
		let err = '';
		if (nam === "age") {
			if (val !== "" && !Number(val)) {
				err = <strong>Your age must be a number</strong>;
			}
		}
		this.setState({ errormessage: err });
		this.setState({ [nam]: val });
	}
	render() {
		return (
			<form onSubmit={this.mySubmitHandler}>
				<h4>Hello {this.state.username} {this.state.age}</h4>
				<div class='fields'>
					<div class='field half'>
						<label for='Name'>Name:</label>
						<input type='text' name='username' onChange={this.myChangeHandler} />
					</div>

					<div class='field half'>
						<label for='Age'>Age:</label>
						<input type='text' name='age' onChange={this.myChangeHandler} />
						{this.state.errormessage}
					</div>
				</div>				
				<p><input type='submit' /></p>
			</form>
		);
	}
}

ReactDOM.render(<MyForm />, document.getElementById('contact-form'));
